import axios from 'axios';

const apiKey = '44fd846a8fbd886b31f763260ef2b77b';
axios.defaults.baseURL = 'https://api.themoviedb.org/3';
export default {
  searchQuery: '',
  page: 1,
  // Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð¿Ð¾Ð²ÐµÑ€Ñ‚Ð°Ñ” Ð¾Ð±'Ñ”ÐºÑ‚ url Ð´Ð»Ñ Ð¾ÑÐ½Ð¾Ð²Ð½Ð¸Ñ… Ð·Ð°Ð¿Ð¸Ñ‚Ñ–Ð²
  getUrl() {
    const urls = {};
    urls.searchUrl = `/search/movie?api_key=44fd846a8fbd886b31f763260ef2b77b&language=en-US&query==${this.query}&page=${this.page}&include_adult=false`;
    urls.popylarFilmsUrl = `/trending/movie/day?api_key=${apiKey}&page=${this.page}`;
    return urls;
  },

  async getUpdatedFilms(url) {
    try {
      // Ð¾Ñ‚Ñ€Ð¸Ð¼ÑƒÑ”Ð¼Ð¾ Ð¼Ð°ÑÐ¸Ð² Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ–Ð² Ð·Ð°Ð¿Ð¸Ñ‚Ñ–Ð² Ð´Ð»Ñ Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð½Ñ Ð¿Ð¾Ð²Ð½Ð¾Ñ— Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ— Ð¿Ñ€Ð¾ Ñ„Ñ–Ð»ÑŒÐ¼Ð¸
      const filmsData = await this.getFullFilmsInfo(url);
      // Ð¿Ñ€Ð¾Ñ…Ð¾Ð´Ð¸Ð¼Ð¾ÑÑ Ð¿Ð¾ Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð¾Ð¼Ñƒ Ð¼Ð°ÑÐ¸Ð²Ñ– Ñ– Ð²Ð¸Ð±Ð¸Ñ€Ð°Ñ”Ð¼Ð¾ Ð²ÑÑ– Ð¾Ð±'Ñ”ÐºÑ‚Ð¸ Ð· Ð¿Ð¾Ð²Ð½Ð¾ÑŽ Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ”ÑŽ Ð¿Ñ€Ð¾ Ñ„Ñ–Ð»ÑŒÐ¼Ð¸
      const films = filmsData.map(({ data }) => data);
      // Ð”Ð»Ñ ÐºÐ¾Ð¶Ð½Ð¾Ð³Ð¾ Ð¾Ð±'Ñ”ÐºÑ‚Ð° Ð¾Ð½Ð¾Ð²Ð»ÑŽÑ”Ð¼Ð¾ Ð½ÐµÐ¾Ð±Ñ…Ñ–Ð´Ð½Ñƒ Ð² Ð½ÑŒÐ¾Ð¼Ñƒ Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–ÑŽ
      const updatedFilms = this.updateInfo(films);
      console.log(updatedFilms);
      return updatedFilms;
    } catch (error) {
      console.log(error);
    }
  },
  // Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð¿Ð¾Ð²ÐµÑ€Ñ‚Ð°Ñ” Ð²ÑÑŽ Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–ÑŽ Ð¿Ð¾ id Ñ„Ñ–Ð»ÑŒÐ¼Ð°, Ð·Ð½Ð°Ð¹Ð´ÐµÐ½Ð¾Ð³Ð¾ Ð·Ð° query
  async getFullFilmsInfo(url) {
    try {
      // Ð’Ð¸Ð±Ð¸Ñ€Ð°Ñ”Ð¼Ð¾ id Ð²ÑÑ–Ñ… Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð¸Ñ… Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð² Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð·Ð° Ð¿ÐµÐ²Ð½Ð¸Ð¼ Ð·Ð°Ð¿Ð¸Ñ‚Ð¾Ð¼
      const idList = await this.getFilmiIdList(url);
      // Ð´Ð»Ñ ÐºÐ¾Ð¶Ð½Ð¾Ð³Ð¾ Ð¾Ð±'Ñ”ÐºÑ‚Ð°, Ð·Ð° Ð¹Ð¾Ð³Ð¾ id, Ñ€Ð¾Ð±Ð¸Ð¼Ð¾ Ñ‰Ðµ Ð¾Ð´Ð¸Ð½ Ð·Ð°Ð¿Ð¸Ñ‚ Ð´Ð»Ñ Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð½Ñ Ð¿Ð¾Ð²Ð½Ð¾Ñ— Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ— Ð¿Ñ€Ð¾ Ñ„Ñ–Ð»ÑŒÐ¼
      const promises = idList.map(id =>
        axios.get(`movie/${id}?api_key=${apiKey}&language=en-US`),
      );
      // Ð¾Ñ‚Ñ€Ð¸Ð¼ÑƒÑ”Ð¼Ð¾ Ð¼Ð°ÑÐ¸Ð² Ð¿Ñ€Ð¾Ð¼Ñ–ÑÑ–Ð² Ð· Ð¾Ð±'Ñ”ÐºÑ‚Ð°Ð¼Ð¸, Ð² ÑÐºÐ¸Ñ… Ñ” Ð¿Ð¾Ð²Ð½Ð° Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ Ð¿Ñ€Ð¾ Ñ„Ñ–Ð»ÑŒÐ¼
      const films = await Promise.all(promises);
      // ÐÐµ Ð·Ð½Ð°ÑŽ Ñ‡Ð¸ Ñ‚ÑƒÑ‚ Ð·Ð±Ñ–Ð»ÑŒÑˆÑƒÑ”Ñ‚ÑŒÑÑ ÑÑ‚Ð¾Ñ€Ñ–Ð½ÐºÐ°, Ð²Ð¶Ðµ Ð³Ð¾Ð»Ð¾Ð²Ð° Ð½Ðµ Ð²Ð°Ñ€Ð¸Ñ‚ÑŒðŸ˜ƒ
      this.page += 1;
      return films;
    } catch (error) {}
  },

  // getUpdatedPopularFilms -  Ð²Ñ‹Ð²Ð¾Ð´ Ð¿Ð¾Ð¿ÑƒÐ»ÑÑ€Ð½Ñ‹Ñ… Ñ„Ð¸Ð»ÑŒÐ¼Ð¾Ð² Ð·Ð° Ð½ÐµÐ´ÐµÐ»ÑŽ
  async getUpdatedPopularFilms() {
    try {
      const filmsData = await this.getFullPopularFilms();
      const films = filmsData.map(({ data }) => data);
      const updatedFilms = this.updateInfo(films);
      return updatedFilms;
    } catch (error) {
      console.log(error);
    }
  },

  //showFilmDetails(id) - Ð¿Ð¾ÐºÐ°Ð· Ð´ÐµÑ‚Ð°Ð»ÑŒÐ½Ð¾Ð¹ Ð¸Ð½Ñ„Ñ‹ Ð¾ Ñ„Ð¸Ð»ÑŒÐ¼Ðµ Ð¿Ñ€Ð¸ Ð¿Ð¾ÐºÐ°Ð·Ðµ Ð¼Ð¾Ð´Ð°Ð»ÐºÐµ, Ð°Ð¹Ð´Ð¸ÑˆÐ½Ð¸Ðº Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½ Ð² Ð»Ð¸ ÐºÐ°Ð¶Ð´Ð¾Ð³Ð¾ Ñ„Ð¸Ð»ÑŒÐ¼Ð° data-id
  async showFilmDetails(id) {
    try {
      const { data } = await axios.get(
        `/movie/${id}?api_key=${apiKey}&language=en-US`,
      );

      return data;
    } catch (error) {
      console.log(error);
    }
  },

  // Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð¿Ð¾Ð²ÐµÑ€Ñ‚Ð°Ñ” id Ð²ÑÑ–Ñ… Ñ„Ñ–Ð»ÑŒÐ¼Ñ–Ð²
  async getFilmiIdList(url) {
    try {
      const { data } = await axios.get(url);
      const { results } = await data;
      const filmIdList = results.map(({ id }) => id);
      return filmIdList;
    } catch (error) {
      console.log(error);
    }
  },

  // Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð¾Ñ‚Ñ€Ð¸Ð¼ÑƒÑ” Ð¼Ð°ÑÐ¸Ð² Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð² Ð· Ð¿Ð¾Ð²Ð½Ð¾ÑŽ Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ”ÑŽ Ð¿Ñ€Ð¾ Ñ„Ñ–Ð»ÑŒÐ¼Ð¸, Ð¾Ð½Ð¾Ð²Ð»ÑŽÑ” Ð½ÐµÐ¾Ð±Ñ…Ñ–Ð´Ð½Ñ– Ð´Ð°Ð½Ñ– Ð´Ð»Ñ ÑÑ‚Ð°Ð²ÐºÐ¸ Ð² ÑˆÐ°Ð±Ð»Ð¾Ð½ Ñ– Ð¿Ð¾Ð²ÐµÑ€Ñ‚Ð°Ñ” Ð½Ð¾Ð²Ð¸Ð¹ Ð¼Ð°ÑÐ¸Ð² Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð² Ð· Ð½ÐµÐ¾Ð±Ñ…Ñ–Ð´Ð½Ð¸Ð¼Ð¸ Ð´Ð°Ð½Ð¸Ð¼Ð¸
  updateInfo(data) {
    // Ð¿Ñ€Ð¾Ñ…Ð¾Ð´Ð¸Ð¼Ð¾ÑÑ Ð¿Ð¾ Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð², Ð´Ð»Ñ ÐºÐ¾Ð¶Ð½Ð¾Ð³Ð¾ Ð¾Ð±'Ñ”ÐºÑ‚Ð° Ñ€Ð¾Ð±Ð¸Ð¼Ð¾ Ð½ÐµÐ¾Ð±Ñ…Ñ–Ð´Ð½Ñ– Ð·Ð¼Ñ–Ð½Ð¸ Ñ– Ð´Ð¾Ð´Ð°Ñ”Ð¼Ð¾ Ñ—Ñ… Ð² Ð½Ð¾Ð²Ð¸Ð¹ Ð¼Ð°ÑÐ¸Ð²
    const updatedInfo = data.reduce((filmsList, film) => {
      // Ð¿ÐµÑ€ÐµÑ‚Ð²Ð¾Ñ€ÑŽÑ”Ð¼Ð¾ release_date Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚ '***' (Ñ€Ñ–Ðº)
      film.release_date = film.release_date.slice(0, -6);
      // Ð¿ÐµÑ€ÐµÑ‚Ð²Ð¾Ñ€ÑŽÑ”Ð¼Ð¾ Ð¼Ð°ÑÐ¸Ð² Ð¾Ð±'Ñ”ÐºÑ‚Ñ–Ð² Ð· Ð¶Ð°Ð½Ñ€Ð°Ð¼Ð¸ Ð· Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ [{ id: name},..., { id: name} ] Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚ [name, ..., name]
      film.genres = this.updateGenres(film.genres);
      // Ð´Ð¾Ð´Ð°Ñ”Ð¼Ð¾ Ð·Ð¼Ñ–Ð½ÐµÐ½Ñ– Ð¾Ð±'Ñ”ÐºÑ‚Ð¸ Ð²  Ð¼Ð°ÑÐ¸Ð²
      if (!film.poster_path) {
        film.poster_path = `https://via.placeholder.com/274x398?text=THE+PICTURE+IS+WANTED`;
      } else {
        film.poster_path = `https://themoviedb.org/t/p/w220_and_h330_face/${film.poster_path}`;
      }
      filmsList.push(film);
      return filmsList;
    }, []);
    return updatedInfo;
  },

  //ÑÐ¾Ð³Ð»Ð°ÑÐ½Ð¾ Ð¼Ð°ÐºÐµÑ‚Ð° ÐµÑÐ»Ð¸ Ð¶Ð°Ð½Ñ€Ð¾Ð² 3 -Ð²Ñ‹ÑÐ²ÐµÑ‡Ð¸Ð²Ð°ÑŽÑ‚ÑÑ Ð²ÑÐµ 3 Ð¶Ð°Ð½Ñ€Ð°,ÐµÑÐ»Ð¸ Ð±Ð¾Ð»ÑŒÑˆÐµ - 2 ÑÐ»Ð¾Ð²Ð° Ð¿Ð»ÑŽÑ Other
  updateGenres(genres) {
    const result = genres.reduce((genresList, genre, index) => {
      const { name } = genre;
      if (index === 3) {
        const tooManyGenres = 'Other';
        genresList[2] = tooManyGenres;
        return genresList;
      } else if (index > 3) {
        return genresList;
      }

      genresList.push(name);
      return genresList;
    }, []);

    return result;
  },

  get query() {
    return this.searchQuery;
  },
  set query(search) {
    this.searchQuery = search;
  },

  resetPage() {
    this.page = 1;
  },
};
